.cart {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: scroll;
  }
  
.total{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
  }
  
.action {
    text-align: right;
  }
  
.action button {
    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #8a2b06;
    padding: 0.5rem 2rem;
    border-radius: 25px;
    margin-left: 1rem;
  }
  
.action button:hover,
.action button:active {
    background-color: #5a1a01;
    border-color: #5a1a01;
    color: white;
  }
  
.action .btnClose {
    color: #8a2b06;
  }
  
.action .order {
    background-color: #8a2b06;
    color: white;
  }